.hljs {
  display: block;
  overflow-x: auto;
  padding: 0.5em;
  background: #202334;
}

.hljs-built_in,
.hljs-selector-tag,
.hljs-section,
.hljs-link {
  color: #8be9fd;
}

.hljs-keyword {
  color: #ff79c6;
}

.hljs,
.hljs-subst {
  color: #f8f8f2;
}

.hljs-title,
.hljs-attr,
.hljs-meta-keyword {
  font-style: italic;
  color: #50fa7b;
}

.hljs-string,
.hljs-meta,
.hljs-name,
.hljs-type,
.hljs-symbol,
.hljs-bullet,
.hljs-addition,
.hljs-variable,
.hljs-template-tag,
.hljs-template-variable {
  color: #f1fa8c;
}

.hljs-comment,
.hljs-quote,
.hljs-deletion {
  color: #6272a4;
}

.hljs-keyword,
.hljs-selector-tag,
.hljs-literal,
.hljs-title,
.hljs-section,
.hljs-doctag,
.hljs-type,
.hljs-name,
.hljs-strong {
  font-weight: bold;
}

.hljs-literal,
.hljs-number {
  color: #bd93f9;
}

.hljs-emphasis {
  font-style: italic;
}

:root {
  --color-primary-dark: 17 24 39;
  --color-primary-light: 75 81 96;
  --color-white: 255 255 255;
  --color-highlight: 255 26 117;
  --header-position: sticky;
  --content-offset: 116px;
  --header-height: 180px;
  --header-mb: -116px;
  --avatar-image-transform: translate3d(0rem, 0, 0) scale(1);
  --avatar-border-transform: translate3d(-0.2222222222222222rem, 0, 0) scale(1.7777777777777777);
  --avatar-border-opacity: 0;
  --header-top: 0px;
  --avatar-top: 0px;
}

html {
  font-family: "Work Sans", sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}
html h1,
html h2,
html h3,
html h4,
html h5,
html h6 {
  font-family: "Wix Madefor Display", serif;
}
html .prose :where(pre):not(:where([class~=not-prose], [class~=not-prose] *)) {
  border-radius: 0.75rem;
  padding: 24px 32px 24px 32px;
}
@media (min-width: 768px) {
  html .prose :where(pre):not(:where([class~=not-prose], [class~=not-prose] *)) {
    width: 60vw;
    position: relative;
    left: -33.3333333%;
  }
}
html code, html pre, html kbd, html samp {
  font-family: "Zed Mono Web", ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace !important;
  font-size: 16px !important;
  font-style: oblique;
}
html body a {
  text-decoration: none !important;
  transition: color 0.25s ease-out, background-color 0.25s ease-out, border-color 0.25s ease-out, box-shadow 0.25s ease-out;
}
html body p {
  font-size: 18px;
}
html body .page h2, html body .post h2, html body .home-intro h2 {
  font-size: 28px;
  color: rgb(var(--color-white));
}
html body .page h3, html body .post h3, html body .home-intro h3 {
  font-size: 22px;
}
html body .page a, html body .post a, html body .home-intro a {
  color: rgb(var(--color-white));
  border-bottom: 1px solid rgb(var(--color-white));
  transition: color 0.25s ease-out, background-color 0.25s ease-out, border-color 0.25s ease-out, box-shadow 0.25s ease-out;
}
html body .page a:hover, html body .post a:hover, html body .home-intro a:hover {
  color: rgb(var(--color-highlight));
  border-bottom: 1px solid rgb(var(--color-highlight));
}
html body .page a.post-button, html body .post a.post-button, html body .home-intro a.post-button {
  color: rgb(var(--color-primary-dark));
  border-bottom-width: 2px;
}
html body .page a.post-button:hover, html body .post a.post-button:hover, html body .home-intro a.post-button:hover {
  border-color: rgb(var(--color-highlight));
  color: rgb(var(--color-highlight));
}
html body .page .kg-card a, html body .post .kg-card a, html body .home-intro .kg-card a {
  text-decoration: none !important;
  border-bottom-width: 0 !important;
}
html body .page .kg-card a:hover, html body .post .kg-card a:hover, html body .home-intro .kg-card a:hover {
  text-decoration: none !important;
  border-bottom-width: 0 !important;
}
html body .page .kg-gallery-card, html body .post .kg-gallery-card, html body .home-intro .kg-gallery-card {
  left: -80%;
}
html body .page .kg-gallery-card figcaption, html body .post .kg-gallery-card figcaption, html body .home-intro .kg-gallery-card figcaption {
  display: none;
}
@media (min-width: 768px) {
  html body .kg-gallery-card {
    width: 100vw;
    left: -90%;
    position: relative;
  }
  html body .kg-gallery-container {
    display: flex;
    overflow: visible;
    gap: 1.5rem;
    justify-content: center;
    min-height: 300px;
  }
  html body .kg-gallery-container .kg-gallery-row {
    display: flex;
    overflow: visible;
    gap: 0.5rem;
    justify-content: center;
  }
  html body .kg-gallery-container .kg-gallery-row:not(:first-of-type) {
    margin: 0 !important;
  }
  html body .kg-gallery-container .kg-gallery-row .kg-gallery-image {
    position: relative;
    aspect-ratio: 9/10;
    width: 18rem;
    flex: none !important;
    overflow: hidden;
    border-radius: 1rem;
  }
  html body .kg-gallery-container .kg-gallery-row .kg-gallery-image img {
    position: absolute;
    width: 100%;
    height: 100%;
    inset: 0;
    object-fit: cover;
    color: transparent;
  }
}
html body figure {
  display: flex;
  flex-direction: column;
}
html body figure.kg-card img {
  margin-top: 0 !important;
  margin-bottom: 0 !important;
  border-radius: 12px;
}
html body figure img {
  object-fit: cover;
  width: auto;
  height: auto;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
html body figure figcaption {
  padding: 4px 8px;
  font-family: "Work Sans", serif;
  font-size: 14px;
  font-style: italic;
  color: rgb(var(--color-white)/40%);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  background: rgb(var(--color-primary-dark)/20%);
}
html body figure figcaption p {
  font-size: 14px;
  text-align: center;
}

.kg-width-wide img {
  max-width: 85vw;
}
.kg-width-full img {
  max-width: 100vw;
}

p {
  padding-bottom: 12px;
}